import React from "react";
import Backdrop from "@material-ui/core/Backdrop";
import CircularProgress from "@material-ui/core/CircularProgress";
import { Container } from "@material-ui/core";
import { DropzoneArea } from "material-ui-dropzone";
import { makeStyles } from "@material-ui/core/styles";
const PDFJS = require("pdfjs-dist/webpack");

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: "#fff",
  },
}));
const PdfToImages = ({ setImages }) => {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const base64ToImages = async (rawData) => {
    const images = [];
    const pdf = await PDFJS.getDocument(rawData).promise;
    const canvas = document.createElement("canvas");
    for (let i = 0; i < pdf.numPages; i++) {
      const page = await pdf.getPage(i + 1);
      const viewport = page.getViewport({ scale: 1 });
      const context = canvas.getContext("2d");
      canvas.height = viewport.height;
      canvas.width = viewport.width;
      await page.render({ canvasContext: context, viewport: viewport }).promise;
      images.push(canvas.toDataURL());
    }

    canvas.remove();
    setOpen(false);
    setImages(images);
  };

  return (
    <Container>
      <DropzoneArea
        clearOnUnmount={true}
        showAlerts={["error"]}
        filesLimit={1}
        acceptedFiles={[".pdf"]}
        maxFileSize={9999999999999999999999999999}
        onChange={(files) => {
          let selectedFile = files;
          let file = null;
          if (selectedFile.length > 0) {
            let fileToLoad = selectedFile[0];
            let fileReader = new FileReader();
            fileReader.onload = function (fileLoadedEvent) {
              setOpen(true);
              file = fileLoadedEvent.target.result;
              base64ToImages(file);
            };
            fileReader.readAsDataURL(fileToLoad);
          }
        }}
      />
      <Backdrop className={classes.backdrop} open={open} color="secondary">
        <CircularProgress size={100} color="secondary" />
      </Backdrop>
    </Container>
  );
};

export default PdfToImages;
