import { Container, Button } from "@material-ui/core";
import React from "react";
import HTMLFlipBook from "react-pageflip";
import { makeStyles } from "@material-ui/core/styles";
import Pagination from "@material-ui/lab/Pagination";
import RotateLeftIcon from "@material-ui/icons/RotateLeft";
const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      marginBottom: theme.spacing(1),
    },
    overflow: "hidden",
    border: "3px solid #f50057",
  },
}));

export default function ImagesToBook({ pages, reset }) {
  const [page, setPage] = React.useState(0);
  const ref = React.useRef(null);
  const pRef = React.useRef(null);

  const settings = {
    width: 550,
    height: 733,
    size: "stretch",
    minWidth: 315,
    maxWidth: 1000,
    minHeight: 420,
    maxHeight: 1350,
    maxShadowOpacity: 0.5,
    showCover: false,
    mobileScrollSupport: true,
    usePortrait: true,
    autoSize: true,
  };

  const onPageChange = (e, page) => {
    page = page - 1;
    if (page % 2 === 0) {
      let Book = ref.current.getPageFlip();
      Book.flip(page, "top");
      if (page === Book.getPageCount() - 1 || page === 0)
        Book.flip(page, "top");
      setPage(page);
      console.log("setting page = ", page);
    } else {
      let Book = ref.current.getPageFlip();
      Book.flip(page - 1, "top");
      if (page === Book.getPageCount() - 1 || page === 1)
        Book.flip(page - 1, "top");
      setPage(page - 1);
    }
  };

  const classes = useStyles();

  return (
    <Container className={classes.root}>
      <HTMLFlipBook
        ref={ref}
        {...settings}
        onFlip={({ data }) => {
          data = data + 1;
          let Pagination = pRef.current;
          Pagination.childNodes[0].childNodes.forEach((el) => {
            if (el.childNodes[0].innerText === `${data}`) {
              el.classList.add("Mui-selected");
              el.childNodes[0].classList.add("Mui-selected");
              el.childNodes[0].setAttribute("aria-current", "true");
            } else {
              el.classList.remove("Mui-selected");
              el.childNodes[0].classList.remove("Mui-selected");
              el.childNodes[0].setAttribute("aria-current", "false");
            }
          });
        }}
      >
        {pages.map((src, i) => (
          <img src={src} alt={`Page ${i}`} />
        ))}
      </HTMLFlipBook>
      <div style={{ float: "left" }}>
        <Button onClick={reset} variant="outlined" color="secondary">
          Reset <RotateLeftIcon />
        </Button>
      </div>
      <div style={{ float: "right" }}>
        <Pagination
          ref={pRef}
          value={page}
          count={pages.length}
          color="secondary"
          onChange={onPageChange}
        />
      </div>
    </Container>
  );
}
