import React, { useState } from "react";
import { PdfToImages, ImagesToBook } from "./Components";
import { Container } from "@material-ui/core";

function App() {
  const [images, setImages] = useState([]);

  return (
    <>
      <Container className="main">
        {!images.length ? (
          <div className="input-box">
            <PdfToImages setImages={(images) => setImages(images)} />
          </div>
        ) : null}
        {images.length ? (
          <ImagesToBook
            pages={images}
            reset={() => setImages([])}
            onClear={() => {
              setImages([]);
            }}
          />
        ) : null}
      </Container>
      <div className="footer">Copyright Â© 2021 . By Siddiqa Qureshi</div>
    </>
  );
}

export default App;
